import * as Swarms from "@hervan/swarms";
import { Token } from "../Token";
import { fontSize } from "../theme";

type MarketState = Swarms.Market.Market;

const { BlueprintList } = Swarms.Blueprint;

interface MarketProps {
  market: MarketState;
  draftToken: (...args: any[]) => void;
}

export const Market = ({ market, draftToken }: MarketProps) => (
  <table
    style={{
      fontSize: `${fontSize}vmin`,
    }}
  >
    <thead>
      <tr>
        <th colSpan={2} />
        <th align="left" colSpan={4}>
          market
        </th>
      </tr>
    </thead>
    <tbody>
      <tr>
        <td />
        <td
          style={{
            verticalAlign: "middle",
            textAlign: "right",
          }}
        >
          stalls:
        </td>
        <td colSpan={4}>
          {market.stalls.map((token, i) => (
            <span
              key={i}
              style={{ cursor: "pointer" }}
              onClick={() => draftToken(i)}
            >
              <Token token={token} />
            </span>
          ))}
        </td>
      </tr>
      <tr>
        <td />
        <td
          style={{
            verticalAlign: "middle",
            textAlign: "right",
          }}
        >
          bag:
        </td>
        {BlueprintList.map((token, i) => (
          <td key={i} style={{ textAlign: "right" }}>
            <Token key={i} token={token} />
            {market.bag.filter((t) => t === token).length}
          </td>
        ))}
      </tr>
    </tbody>
  </table>
);
