import { numberlist } from "@hervan/utils";
import { ActionList } from "../Action";
import { Blueprint, BlueprintList } from "../Blueprint";
import { NUM_PLAYERS, Shuffle } from "../common";

export type Market = {
  stalls: Blueprint[];
  bag: Blueprint[];
};

export const newMarket: Market = {
  stalls: [],
  bag: [
    ...numberlist(NUM_PLAYERS * ActionList.length - 1).flatMap(
      (_) => BlueprintList
    ),
    BlueprintList[0],
  ],
};

export const discardMarketToken = (market: Market) => {
  if (market.stalls.length === 0) {
    return market;
  }
  const [token, ...stalls] = market.stalls;
  return {
    ...market,
    stalls,
    bag: [...market.bag, token],
  };
};

export const drawMarketToken = (market: Market, stall: number) => {
  if (market.stalls.length === 0) {
    return market;
  }
  const stalls = market.stalls.filter((_, i) => i !== stall);
  return {
    ...market,
    stalls,
  };
};

const drawOneTokenFromBag = (market: Market, shuffle: Shuffle) => {
  if (market.bag.length === 0) {
    return market;
  }
  const [token, ...bag] = shuffle(market.bag);
  return {
    ...market,
    stalls: [...market.stalls, token],
    bag,
  };
};

export const refillMarketTokens: (
  market: Market,
  shuffle: Shuffle,
  amount: number
) => Market = (market, shuffle, amount) =>
  market.stalls.length === 0
    ? numberlist(amount).reduce(
        (market) => drawOneTokenFromBag(market, shuffle),
        market
      )
    : market;
