import { numberlist, unique } from "@hervan/utils";
import { Player } from "Player";
import { Action, ActionList } from "../../Action";
import { Blueprint } from "../../Blueprint";
import { Slot } from "./Slot";

type GridRow = {
  [k in Action]: Slot<Action>;
};

export const GRID_SIZE = 4;

const emptyGridRow: (rowNumber: number) => GridRow = (rowNumber) =>
  ActionList.reduce(
    (row: GridRow, action) => ({
      ...row,
      [action]: {
        action,
        row: rowNumber,
        blueprint: undefined,
      } as Slot<typeof action>,
    }),
    {} as GridRow
  );

export type TechGrid = GridRow[];

export const playerStartingGrid: () => TechGrid = () =>
  numberlist(GRID_SIZE).map((i) => emptyGridRow(i));

const isInitialPosition = (grid: TechGrid, row: number, action: Action) =>
  row === 0 &&
  action === Action.Spread &&
  grid[row][action].blueprint === undefined;

const isAdjacentToExistingUpgrade = (
  grid: TechGrid,
  row: number,
  col: number
) =>
  [
    ...(row === 0 ? [] : [grid[row - 1][ActionList[col]]]),
    ...(row === GRID_SIZE - 1 ? [] : [grid[row + 1][ActionList[col]]]),
    ...(col === 0 ? [] : [grid[row][ActionList[col - 1]]]),
    ...(col === GRID_SIZE - 1 ? [] : [grid[row][ActionList[col + 1]]]),
  ].some((s) => s.blueprint !== undefined);

export const isUniqueBlueprint = (
  grid: TechGrid,
  row: number,
  action: Action,
  blueprint: Blueprint
) =>
  grid[row][action].blueprint === blueprint ||
  (grid.every((row) => row[action].blueprint !== blueprint) &&
    ActionList.every((a) => grid[row][a].blueprint !== blueprint));

export const isValidActionSelection = (
  { grid, hand }: Player,
  row: number,
  action: Action,
  blueprint: Blueprint
) => {
  const col = ActionList.findIndex((a) => a === action);
  const slot = grid[row][action];

  const previouslyOccupiedSpace = slot.blueprint === blueprint;
  const uniquenessRequirement = isUniqueBlueprint(grid, row, action, blueprint);
  const adjacencyRequirement =
    isInitialPosition(grid, row, action) ||
    isAdjacentToExistingUpgrade(grid, row, col);
  const properNewlyOccupiedSpace =
    slot.blueprint === undefined &&
    uniquenessRequirement &&
    adjacencyRequirement;
  const tradeRequirement =
    action !== Action.Trade ||
    hand.filter((t) => t === blueprint).length >=
      1 + (properNewlyOccupiedSpace ? 1 : 0);
  const researchRequirement =
    action !== Action.Research ||
    hand.filter((t) => t === blueprint).length >=
      3 + (properNewlyOccupiedSpace ? 1 : 0);

  return (
    (previouslyOccupiedSpace || properNewlyOccupiedSpace) &&
    tradeRequirement &&
    researchRequirement
  );
};

export const blueprintKnowledge: (
  grid: TechGrid,
  blueprint: Blueprint | undefined
) => number = (grid, blueprint) =>
  blueprint === undefined
    ? 0
    : grid
        .flatMap((row) => ActionList.map((action) => row[action]))
        .filter((action) => action.blueprint === blueprint).length;

export const upgradeGrid = (
  grid: TechGrid,
  slot: Slot<Action>,
  token: Blueprint
): TechGrid =>
  grid.map((originalRow, i) =>
    ActionList.reduce(
      (newRow, action) => ({
        ...newRow,
        [action]:
          i === slot.row && action === slot.action
            ? {
                ...originalRow[action],
                blueprint: token,
              }
            : originalRow[action],
      }),
      originalRow
    )
  );

export const upgradePlayerGrid: (player: Player) => Player = (
  player: Player
) => {
  const slot = player.selection.slot;
  if (slot === undefined) {
    return player;
  }
  const token = player.selection.blueprint;
  if (token === undefined) {
    return player;
  }
  if (slot.blueprint !== undefined) {
    return player;
  }
  const hand = player.hand.filter((v, i, a) => v !== token || !unique(v, i, a));
  const grid = upgradeGrid(player.grid, slot, token);
  return { ...player, grid, hand };
};

export * as Slot from "./Slot";
