import { Action, Selection, noSelection } from "../Action";
import { Blueprint, BlueprintList } from "../Blueprint";
import { Coordinates } from "../Map";
import { Track } from "../Track";
import { TechGrid, playerStartingGrid } from "./Grid";
import { Species, SpeciesList } from "./Species";

export type Player = {
  id: number;
  species: Species;
  blueprint: Blueprint;
  corner: Coordinates;
  timeSpent: number;
  coins: number;
  swarm: Coordinates[];
  city: Coordinates[][];
  selection: Selection<Action>;
  grid: TechGrid;
  delivery: number;
  hand: Blueprint[];
};

export const newPlayer: (id: number, corner: Coordinates) => Player = (
  id,
  corner
) => ({
  id,
  species: SpeciesList[id],
  blueprint: BlueprintList[BlueprintList.length - id - 1],
  corner,
  timeSpent: 0,
  coins: 0,
  swarm: [],
  city: [],
  selection: noSelection,
  grid: playerStartingGrid(),
  delivery: 0,
  hand: [],
});

const tiebreaker = (p1: Player, p2: Player) =>
  p1.coins > p2.coins
    ? p1
    : p1.coins < p2.coins
    ? p2
    : p1.timeSpent < p2.timeSpent
    ? p1
    : p2;

export const winner = (players: Player[]) => players.reduce(tiebreaker);

export const nextPlayer = (
  players: Player[],
  turn: Track,
  gameEndTriggered?: number
) => nextPlayers(players, turn, gameEndTriggered)[0];

export const nextPlayers = (
  players: Player[],
  turn: Track,
  gameEndTriggered?: number
) =>
  turn[
    players.reduce(
      (acc, { timeSpent }) => Math.min(timeSpent, acc),
      gameEndTriggered || players[0].timeSpent
    )
  ];

export * as Grid from "./Grid";
export * as Species from "./Species";
