import * as Swarms from "@hervan/swarms";
import { deepAssign, numberlist, partition, unique } from "@hervan/utils";
import type { Game, Move } from "boardgame.io";
import { INVALID_MOVE } from "boardgame.io/core";

type Action = Swarms.Action.Action;
type Blueprint = Swarms.Blueprint.Blueprint;
type Coordinates = Swarms.Map.Coordinates;
type TerrainMap = Swarms.Map.TerrainMap;
type Player = Swarms.Player.Player;
type Track = Swarms.Track.Track;

const { Action, ActionList, noSelection } = Swarms.Action;
const {
  getAllBlueprintCoordinatesWithTranslation,
  validateBuildBlueprint,
  validateManufactureBlueprint,
  validateDeliveryBlueprint,
  validateSpreadingBlueprint,
} = Swarms.Blueprint;
const {
  GAME_SIZE,
  VECTOR_ORIENTATIONS,
  addCoordinates,
  applyMapEffect,
  containsCoord,
  getAdjacentToSelection,
  newMap,
  raiseMap,
  sameCoordinates,
  smoothMap,
  startCorners,
} = Swarms.Map;
const { drawMarketToken, newMarket, refillMarketTokens } = Swarms.Market;
const { newPlayer, nextPlayer, nextPlayers, winner } = Swarms.Player;
const {
  blueprintKnowledge,
  isValidActionSelection,
  upgradeGrid,
  upgradePlayerGrid,
} = Swarms.Player.Grid;
const { getActionRunner, scienceScore } = Swarms.Player.Grid.Slot;
const { moveOnTurnTrack } = Swarms.Track;

const GAME_LENGTH = 2;

export type SwarmsState = Swarms.SwarmsState;

const playerSetupSwarm: (
  G: SwarmsState,
  token: Blueprint,
  id: number
) => SwarmsState = (G, token, id) => {
  const currentPlayer = G.players[id];
  const grid = upgradeGrid(
    currentPlayer.grid,
    currentPlayer.grid[0][Action.Spread],
    token
  );
  const move = getSpreadingPattern(G.map, token, currentPlayer)[0];
  const player = { ...currentPlayer, grid, swarm: move };
  const map = applyMapEffect(G.map, move, { owner: id, state: "occupied" });
  return deepAssign({ ...G, map }, player, "players", (p) => p.id === id);
};

const playerSetupState: (G: SwarmsState) => SwarmsState = (G) =>
  G.players.reduce(
    (acc, _, i) => playerSetupSwarm(acc, G.players[i].blueprint, i),
    G
  );

const triggerGameEnd = (G: SwarmsState, id: number) =>
  G.gameEndTriggered === undefined &&
  nextPlayer(G.players, G.turnTrack) !== id &&
  G.players[id].grid.filter(
    (row) => row[Action.Research].blueprint !== undefined
  ).length >= GAME_LENGTH;

export const passTime: (
  G: SwarmsState,
  id: number
) => SwarmsState | undefined = (G, id) => {
  const currentPlayer = G.players[id];
  if (currentPlayer === undefined) {
    return G;
  }
  const timeTaken =
    blueprintKnowledge(
      currentPlayer.grid,
      currentPlayer.selection.slot?.blueprint
    ) + currentPlayer.selection.time;
  const timeSpent = currentPlayer.timeSpent + timeTaken;
  const turnTrack = moveOnTurnTrack(
    G.turnTrack,
    currentPlayer.timeSpent,
    timeSpent
  );

  const [hand, discardedTokens] = partition(
    currentPlayer.hand,
    (t) => t !== currentPlayer.selection.blueprint
  );
  const player = { ...currentPlayer, timeSpent, hand };
  const market = { ...G.market, bag: [...G.market.bag, ...discardedTokens] };

  const nextG = {
    ...G,
    players: G.players.map((p) => (p.id === currentPlayer.id ? player : p)),
    market,
    turnTrack,
  };

  if (triggerGameEnd(nextG, currentPlayer.id)) {
    return { ...nextG, gameEndTriggered: timeSpent };
  }
  return nextG;
};

const draftToken: Move<SwarmsState> = ({ G, events, playerID }, i: number) => {
  const id = parseInt(playerID || "0");
  const currentPlayer = G.players[id];
  const token = G.market.stalls[i];
  const market = drawMarketToken(G.market, i);
  const time = currentPlayer.selection.time + 1;
  const drawTokenGame = passTime(
    deepAssign(
      { ...G, market },
      {
        ...currentPlayer,
        hand: [...currentPlayer.hand, token],
        selection: { ...noSelection, time },
      },
      "players",
      (p) => p.id === id
    ),
    id
  );
  if (drawTokenGame === undefined) return invalidMove();
  events.endTurn();
  return drawTokenGame;
};

const selectToken: Move<SwarmsState> = ({ G, playerID }, token: Blueprint) => {
  const id = parseInt(playerID || "0");
  return doTokenSelection(G, id, token);
};

const doTokenSelection = (G: SwarmsState, id: number, token: Blueprint) =>
  deepAssign(G, token, "players", (p) => p.id === id, "selection", "blueprint");

const selectSlot: Move<SwarmsState> = (
  { G, playerID, events },
  row: number,
  action: Action
) => {
  const id = parseInt(playerID || "0");
  const currentPlayer = G.players[id];
  if (currentPlayer.selection.blueprint === undefined) {
    return invalidMove();
  }
  const token = currentPlayer.selection.blueprint;
  const slot = currentPlayer.grid[row][action];
  if (isValidActionSelection(currentPlayer, row, action, token)) {
    const selection = {
      ...currentPlayer.selection,
      slot,
    };
    const selectedSlotG = deepAssign(
      G,
      { ...currentPlayer, selection },
      "players",
      (p) => p.id === id
    );
    if (action === Action.Research) {
      events.endTurn();
      return confirm(selectedSlotG, id);
    }
    return selectedSlotG;
  }
  return invalidMove();
};

const selectLocation: Move<SwarmsState> = (
  { G, playerID, events },
  coords: Coordinates[],
  action: Action
) => {
  const id = parseInt(playerID || "0");
  const currentPlayer = G.players[id];
  const token = currentPlayer.selection.blueprint;
  if (containsCoord(currentPlayer.selection.location, coords[0])) {
    return deepAssign(
      G,
      currentPlayer.selection.location.filter(
        (c) => !sameCoordinates(c, coords[0])
      ),
      "players",
      (p) => p.id === id,
      "selection",
      "location"
    );
  }
  const location = [...currentPlayer.selection.location, ...coords];
  const selection = {
    ...currentPlayer.selection,
    location,
  };
  const validation =
    action === Action.Spread
      ? validateSpreadingBlueprint
      : action === Action.Build
      ? validateBuildBlueprint
      : action === Action.Trade
      ? validateManufactureBlueprint
      : () => false;
  const valid = validation(G.map, selection.location, token, currentPlayer);
  if (valid) {
    if (location.length === 4 && action !== Action.Trade) {
      const validG = deepAssign(
        G,
        { ...selection, location: [], destination: [...selection.location] },
        "players",
        (p) => p.id === id,
        "selection"
      );
      events.endTurn();
      return confirm(validG, id);
    }
    const validG = deepAssign(
      G,
      selection,
      "players",
      (p) => p.id === id,
      "selection"
    );
    return validG;
  }
  return invalidMove();
};

const deselectLocation: Move<SwarmsState> = (
  { G, playerID },
  coord: Coordinates
) => {
  const id = parseInt(playerID || "0");
  return deepAssign(
    G,
    G.players[id].selection.location.filter((c) => !sameCoordinates(c, coord)),
    "players",
    (p) => p.id === id,
    "selection",
    "location"
  );
};

const selectDestination: Move<SwarmsState> = (
  { G, playerID, events },
  coords: Coordinates[]
) => {
  const id = parseInt(playerID || "0");
  const currentPlayer = G.players[id];
  const destination = [...currentPlayer.selection.destination, ...coords];
  if (
    validateDeliveryBlueprint(
      G.map,
      currentPlayer.selection.blueprint,
      currentPlayer.selection.location,
      destination,
      id
    )
  ) {
    const selection = {
      ...currentPlayer.selection,
      destination,
    };
    const validG = deepAssign(
      G,
      selection,
      "players",
      (p) => p.id === id,
      "selection"
    );
    if (destination.length === 4) {
      events.endTurn();
      return confirm(validG, id);
    }
    return validG;
  }
  return invalidMove();
};

const deselectDestination: Move<SwarmsState> = (
  { G, playerID },
  coord: Coordinates
) => {
  const id = parseInt(playerID || "0");
  return deepAssign(
    G,
    G.players[id].selection.destination.filter(
      (c) => !sameCoordinates(c, coord)
    ),
    "players",
    (p) => p.id === id,
    "selection",
    "destination"
  );
};

const debugRaiseMap: Move<SwarmsState> = ({ G, random }) => {
  const originalTerrains = G.map.map((row) => row.map((col) => col.type));
  const raisedTerrains = raiseMap(originalTerrains, random.Number, 1);
  const improvedMap = raisedTerrains.map((row, i) =>
    row.map((type, j) => ({ ...G.map[i][j], type }))
  );
  return { ...G, map: improvedMap };
};

const debugSmoothMap: Move<SwarmsState> = ({ G }) => {
  const originalTerrains = G.map.map((row) => row.map((col) => col.type));
  const smoothTerrains = smoothMap(originalTerrains);
  const improvedMap = smoothTerrains.map((row, i) =>
    row.map((type, j) => ({ ...G.map[i][j], type }))
  );
  return { ...G, map: improvedMap };
};

const confirm: (
  G: SwarmsState,
  id: number
) => SwarmsState | "INVALID_MOVE" | undefined = (G, id) => {
  const currentPlayer = G.players[id];
  const slot = currentPlayer.selection.slot;
  if (slot === undefined) {
    return invalidMove();
  }
  const action = slot.action;

  const player = upgradePlayerGrid(currentPlayer);

  const runner = getActionRunner(action);
  const actionGame = passTime(
    runner(
      deepAssign(G, player, "players", (p) => p.id === id),
      id
    ),
    id
  );

  if (actionGame === undefined) return invalidMove();
  return actionGame;
};

const resetSelection = (G: SwarmsState, id: number) =>
  deepAssign(G, noSelection, "players", (p) => p.id === id, "selection");

const discardSelection: Move<SwarmsState> = ({ G, playerID }) =>
  resetSelection(G, parseInt(playerID || "0"));

const distanceToCorner = (p: Player, c: Coordinates) => {
  const corner = startCorners()[p.id];
  return Math.abs(c.col - corner.col) + Math.abs(c.row - corner.row);
};

const sortDistanceToCorner = (
  p: Player,
  a: Coordinates[],
  b: Coordinates[]
) => {
  const aDistance = Math.min(...a.map((aC) => distanceToCorner(p, aC)));
  const bDistance = Math.min(...b.map((bC) => distanceToCorner(p, bC)));
  return aDistance === bDistance
    ? Math.abs(a[0].row - a[0].col) - Math.abs(b[0].row - b[0].col)
    : aDistance - bDistance;
};

const getSpreadingPattern = (
  map: TerrainMap,
  blueprint: Blueprint,
  player: Player
) =>
  getAdjacentToSelection(
    player.swarm.length > 0 ? player.swarm : [startCorners()[player.id]]
  )
    .flatMap((c) =>
      getAllBlueprintCoordinatesWithTranslation(blueprint, c).filter(
        (bp) =>
          bp.some((cBlueprint) => sameCoordinates(c, cBlueprint)) &&
          validateSpreadingBlueprint(map, bp, blueprint, player)
      )
    )
    .sort((a, b) => sortDistanceToCorner(player, a, b))
    .slice(0, 1);

const getBuildPatterns = (
  map: TerrainMap,
  blueprint: Blueprint,
  player: Player
) =>
  player.swarm
    .flatMap((c) =>
      getAllBlueprintCoordinatesWithTranslation(blueprint, c).filter(
        (bp) =>
          bp.some((cBlueprint) => sameCoordinates(c, cBlueprint)) &&
          validateBuildBlueprint(map, bp, blueprint, player)
      )
    )
    .sort((a, b) => sortDistanceToCorner(player, a, b))
    .slice(0, 1);

const getManufacturePatterns = (
  map: TerrainMap,
  blueprint: Blueprint,
  player: Player
) =>
  player.swarm
    .flatMap((c) =>
      getAllBlueprintCoordinatesWithTranslation(blueprint, c).filter(
        (bp) =>
          bp.some((cBlueprint) => sameCoordinates(c, cBlueprint)) &&
          validateManufactureBlueprint(map, bp, blueprint, player)
      )
    )
    .sort((a, b) => sortDistanceToCorner(player, b, a));

const getDeliveryDestination: (
  map: TerrainMap,
  location: Coordinates[],
  moveUnit: Coordinates,
  blueprint: Blueprint,
  id: number
) => Coordinates[] | undefined = (map, location, moveUnit, blueprint, id) =>
  numberlist(GAME_SIZE).reduce(
    (acc: { last: Coordinates[]; furthest: Coordinates[] | undefined }, _) => {
      const next = acc.last.map((c: Coordinates) =>
        addCoordinates(c, moveUnit)
      );
      return {
        last: next,
        furthest: validateDeliveryBlueprint(map, blueprint, location, next, id)
          ? next
          : acc.furthest,
      };
    },
    { last: location, furthest: undefined }
  ).furthest;

export const SwarmsGame: Game<SwarmsState> = {
  setup: ({ random, ctx }) => {
    const map: TerrainMap = newMap(random.Number);

    const playerOrder = numberlist(ctx.numPlayers);
    const players: Player[] = playerOrder.map((id) =>
      newPlayer(id, startCorners()[id])
    );
    const turnTrack: Track = [[...playerOrder]];
    const market = newMarket;
    const gameEndTriggered = undefined;

    const initialPlayerGame = {
      map,
      market,
      players,
      turnTrack,
      gameEndTriggered,
    };

    return playerSetupState(initialPlayerGame);
  },

  turn: {
    onBegin: ({ G, ctx, random }) => {
      const id = parseInt(ctx.currentPlayer);
      const currentPlayer = G.players[id];
      const market = refillMarketTokens(
        G.market,
        random.Shuffle,
        nextPlayers(G.players, G.turnTrack, G.gameEndTriggered).length
      );
      return deepAssign(
        { ...G, market },
        currentPlayer.selection,
        "players",
        (p) => p.id === id,
        "selection"
      );
    },
    order: {
      first: () => 0,
      next: ({ G }) => nextPlayer(G.players, G.turnTrack, G.gameEndTriggered),
    },
    onEnd: ({ G }) => ({
      ...G,
      players: G.players.map((p) => ({ ...p, selection: noSelection })),
    }),
  },

  moves: {
    draftToken,
    selectToken,
    selectSlot,
    selectLocation,
    deselectLocation,
    selectDestination,
    deselectDestination,
    discardSelection,
    debugRaiseMap,
    debugSmoothMap,
  },

  endIf: ({ G: { players, gameEndTriggered } }) => {
    if (gameEndTriggered !== undefined) {
      if (players.every((p) => p.timeSpent >= gameEndTriggered)) {
        return winner(players);
      }
    }
  },

  onEnd: ({ G }) => {
    const players = G.players.map((player) => {
      const coins = scienceScore(player, G.map).reduce((acc, score, i) => {
        const bpm = blueprintKnowledge(
          player.grid,
          player.grid[i][Action.Research].blueprint
        );
        const res = score * bpm;
        return acc + res;
      }, player.coins);
      return { ...player, coins };
    });
    console.log({
      "player scores": players.map((p) => p.coins),
      "time consumed": G.gameEndTriggered,
    });
    return { ...G, players };
  },

  ai: {
    enumerate: (G, _ctx, playerID) => {
      const id = parseInt(playerID || "0");
      const currentPlayer = G.players[id];
      const selection = currentPlayer.selection;
      const token = selection.blueprint;
      if (token === undefined) {
        const moves = currentPlayer.hand
          .filter(unique)
          .map((t) => ({ move: "selectToken", args: [t] }));
        if (moves.length === 0) {
          return [{ move: "draftToken", args: [0] }];
        }
        return moves;
      }
      if (selection.slot === undefined) {
        const moves = currentPlayer.grid.flatMap((_, i) =>
          ActionList.filter((action) =>
            isValidActionSelection(currentPlayer, i, action, token)
          ).map((action) => ({
            move: "selectSlot",
            args: [i, action],
          }))
        );
        if (moves.length === 0) {
          return [{ move: "draftToken", args: [0] }];
        }
        return moves;
      }
      if (selection.location.length < 4) {
        switch (selection.slot.action) {
          case Action.Spread: {
            const moves = getSpreadingPattern(G.map, token, currentPlayer).map(
              (pattern) => ({
                move: "selectLocation",
                args: [pattern, Action.Spread],
              })
            );
            if (moves.length === 0) {
              return [{ move: "draftToken", args: [0] }];
            }
            return moves;
          }
          case Action.Build: {
            const moves = getBuildPatterns(G.map, token, currentPlayer).map(
              (pattern) => ({
                move: "selectLocation",
                args: [pattern, Action.Build],
              })
            );
            if (moves.length === 0) {
              return [{ move: "draftToken", args: [0] }];
            }
            return moves;
          }
          case Action.Trade: {
            const moves = getManufacturePatterns(G.map, token, currentPlayer)
              .filter(
                (pattern) =>
                  VECTOR_ORIENTATIONS.map((moveUnit) =>
                    getDeliveryDestination(G.map, pattern, moveUnit, token, id)
                  ).filter((pattern) => pattern !== undefined).length > 0
              )
              .map((pattern) => ({
                move: "selectLocation",
                args: [pattern, Action.Trade],
              }));
            if (moves.length === 0) {
              return [{ move: "draftToken", args: [0] }];
            }
            return moves;
          }
          case Action.Research: {
            return [{ move: "draftToken", args: [0] }];
          }
        }
      }
      if (selection.destination.length < 4) {
        if (selection.slot.action === Action.Trade) {
          const moves = VECTOR_ORIENTATIONS.map((moveUnit) =>
            getDeliveryDestination(
              G.map,
              selection.location,
              moveUnit,
              token,
              id
            )
          )
            .filter((pattern) => pattern !== undefined)
            .map((pattern) => ({
              move: "selectDestination",
              args: [pattern],
            }));
          if (moves.length === 0) {
            return [{ move: "draftToken", args: [0] }];
          }
          return moves;
        }
      }
      return [{ move: "draftToken", args: [0] }];
    },
  },
};

const invalidMove = () => {
  const a = true || false;
  if (a) return INVALID_MOVE;
};
