import { deepAssign, partition, unique } from "@hervan/utils";
import { Blueprint } from "Blueprint";
import { SwarmsState } from "index";
import { blueprintKnowledge, upgradeGrid } from "..";
import { Action, ActionList } from "../../../Action";
import {
  Coordinates,
  TerrainMap,
  applyMapEffect,
  clusterCity,
  getAdjacentCoordinates,
  getAdjacentToSelection,
  sameCoordinates,
} from "../../../Map";
import { terrainFromMap } from "../../../Map/Terrain";
import { Player } from "../../../Player";

export type Slot<T extends Action> = {
  action: T;
  row: number;
  blueprint?: Blueprint;
};

type ActionRunner = (G: SwarmsState, id: number) => SwarmsState;

const spread: ActionRunner = (G, id) => {
  const currentPlayer = G.players[id];
  const slot = currentPlayer.selection.slot;
  const token = currentPlayer.selection.blueprint;
  if (slot === undefined || token === undefined) {
    return G;
  }
  const destination = currentPlayer.selection.destination;
  const map = applyMapEffect(G.map, destination, {
    state: "occupied",
    owner: id,
  });
  const strength = blueprintKnowledge(currentPlayer.grid, token);
  const coins = currentPlayer.coins + strength;
  const time = currentPlayer.selection.time + strength;
  const swarm = [...currentPlayer.swarm, ...destination];
  const player = deepAssign(
    { ...currentPlayer, swarm, coins },
    time,
    "selection",
    "time"
  );
  return deepAssign({ ...G, map }, player, "players", (p) => p.id === id);
};

const build: ActionRunner = (G, id) => {
  const currentPlayer = G.players[id];
  const slot = currentPlayer.selection.slot;
  const token = currentPlayer.selection.blueprint;
  if (slot === undefined || token === undefined) {
    return G;
  }
  const destination = currentPlayer.selection.destination;
  const map = applyMapEffect(G.map, destination, {
    state: "built",
    blueprint: token,
  });
  const strength = blueprintKnowledge(currentPlayer.grid, token);
  const coins = currentPlayer.coins + strength;
  const city = clusterCity([...currentPlayer.city, destination]);
  const swarm = currentPlayer.swarm.filter(
    (c1) => !destination.some((c2: Coordinates) => sameCoordinates(c1, c2))
  );
  const time = currentPlayer.selection.time + strength;
  const player = deepAssign(
    { ...currentPlayer, city, swarm, coins },
    time,
    "selection",
    "time"
  );
  const adjacentOpponents = getAdjacentToSelection(
    currentPlayer.selection.destination
  ).map((c) => terrainFromMap(G.map, c));
  const players = G.players.map((p) =>
    p.id === id
      ? player
      : adjacentOpponents.some(
          (t) => t.state === "occupied" && t.owner === p.id
        )
      ? { ...p, city: clusterCity([...p.city, destination]) }
      : p
  );
  return { ...G, map, players };
};

const trade: ActionRunner = (G, id) => {
  const currentPlayer = G.players[id];
  const slot = currentPlayer.selection.slot;
  const token = currentPlayer.selection.blueprint;
  if (slot === undefined || token === undefined) {
    return G;
  }
  const map = applyMapEffect(G.map, currentPlayer.selection.destination, {
    state: "occupied",
    owner: id,
  });
  const [productTokens, handRemainder] = partition(
    currentPlayer.hand,
    (t) => t === token
  );
  const targetOpponents = getAdjacentToSelection(
    currentPlayer.selection.destination
  )
    .map((c) => terrainFromMap(G.map, c))
    .map((t) => (t.state === "occupied" ? t.owner : id))
    .filter((playerId) => playerId !== id)
    .sort();
  const totalSales = productTokens.length;
  const targetDeliveries = targetOpponents
    .filter(unique)
    .map((targetId) => G.players[targetId])
    .map((player, i, a) => {
      const tokens = Math.trunc(totalSales / a.length);
      const remainder = totalSales % a.length;
      return {
        ...player,
        tokens: tokens + (i < remainder ? 1 : 0),
      };
    }) as (Player & {
    tokens: number;
  })[];
  const strength = blueprintKnowledge(currentPlayer.grid, token);
  const price = strength * targetOpponents.length;
  const sale = price * totalSales;
  const coins = sale + currentPlayer.coins;
  const swarm = [
    ...currentPlayer.swarm,
    ...currentPlayer.selection.destination,
  ];
  const delivery = currentPlayer.delivery + totalSales;
  const time = currentPlayer.selection.time + strength;
  const player = deepAssign(
    {
      ...currentPlayer,
      swarm,
      delivery,
      coins,
      hand: [...handRemainder],
    },
    time,
    "selection",
    "time"
  );
  const players = G.players.map((p) => {
    if (p.id === id) {
      return player;
    }
    const target = targetDeliveries.find((td) => td.id === p.id);
    if (target === undefined) {
      return p;
    }
    const hand = [...p.hand, ...Array(target.tokens).fill(token)];
    return {
      ...p,
      hand,
    };
  });
  return { ...G, map, players };
};

export const scienceScore = (player: Player, map: TerrainMap) => [
  Math.trunc(
    player.swarm.filter((critter) =>
      getAdjacentCoordinates(critter)
        .map((adj) => terrainFromMap(map, adj))
        .some((adj) => adj.state === "occupied" && adj.owner !== player.id)
    ).length / 4
  ),
  Math.max(
    0,
    ...player.city.map((cluster) => Math.trunc(cluster.flat().length / 4))
  ),
  player.delivery,
  player.grid.filter((row) =>
    ActionList.every((a) => row[a].blueprint !== undefined)
  ).length +
    ActionList.filter((a) =>
      player.grid.every((row) => row[a].blueprint !== undefined)
    ).length,
];

const research: ActionRunner = (G, id) => {
  const currentPlayer = G.players[id];
  const slot = currentPlayer.selection.slot;
  const token = currentPlayer.selection.blueprint;
  if (slot === undefined || token === undefined) {
    return G;
  }
  const strength = blueprintKnowledge(currentPlayer.grid, token);
  const coins = currentPlayer.coins + strength;
  const [tokens, handRemainder] = partition(
    currentPlayer.hand,
    (t) => t === token
  );
  const time = currentPlayer.selection.time + strength;
  const hand = [...handRemainder, ...tokens.slice(3)];
  const player = deepAssign(
    { ...currentPlayer, coins, hand },
    time,
    "selection",
    "time"
  );
  const players = G.players.map((p) => {
    if (p.id === id) {
      return player;
    }
    const grid = upgradeGrid(p.grid, p.grid[slot.row][Action.Research], token);
    return { ...p, grid };
  });
  return { ...G, players };
};

export const getActionRunner: (action: Action) => ActionRunner = (action) => {
  switch (action) {
    case Action.Spread:
      return spread;
    case Action.Build:
      return build;
    case Action.Trade:
      return trade;
    case Action.Research:
      return research;
  }
};
