import * as Swarms from "@hervan/swarms";
import { Token } from "Board/Token";
import { COIN_EMOJI, TIME_EMOJI, fontSize } from "../theme";
import { Grid } from "./Grid";

type TerrainMap = Swarms.Map.TerrainMap;
type PlayerState = Swarms.Player.Player;

const { ActionList } = Swarms.Action;

interface PlayerProps {
  map: TerrainMap;
  player: PlayerState;
  selectSlot: (...args: any[]) => void;
  selectToken: (...args: any[]) => void;
  winner: boolean;
  isActive: boolean;
  discardSelection: (...args: any[]) => void;
}

export const Player = ({
  map,
  player,
  selectSlot,
  selectToken,
  winner,
  isActive,
  discardSelection,
}: PlayerProps) => (
  <>
    <table
      style={{
        fontSize: `${fontSize}vmin`,
      }}
    >
      <tbody>
        <tr>
          <td />
          <td
            style={{
              verticalAlign: "middle",
              textAlign: "right",
            }}
          >
            hand:
          </td>
          <td colSpan={4}>
            {player.hand.map((token, i) => (
              <span
                key={i}
                style={{ cursor: "pointer" }}
                onClick={() => selectToken(token)}
              >
                <Token
                  token={token}
                  selected={token === player.selection.blueprint}
                />
              </span>
            ))}
          </td>
        </tr>
      </tbody>
    </table>
    <table
      key={`player${player.id}`}
      style={{
        fontSize: `${fontSize}vmin`,
      }}
    >
      <tbody>
        <tr>
          <td colSpan={4} rowSpan={2} />
          <td colSpan={1} style={{ textAlign: "right" }}>
            {winner ? "🏆" : null}
            {player.coins}✖️{COIN_EMOJI}
          </td>
        </tr>
        <tr>
          <td colSpan={1} style={{ textAlign: "right" }}>
            {player.timeSpent}✖️{TIME_EMOJI}
          </td>
        </tr>
        <tr>
          {ActionList.map((action, i) => (
            <td key={i} style={{ textAlign: "right", width: "20vmin" }}>
              {action}
            </td>
          ))}
          <td style={{ textAlign: "right", width: "20vmin" }}>
            {isActive ? (
              <span style={{ cursor: "pointer" }} onClick={discardSelection}>
                ❎ discard action
              </span>
            ) : (
              <span style={{ float: "right" }}>wait for your turn</span>
            )}
          </td>
        </tr>
        <Grid map={map} player={player} selectSlot={selectSlot} />
      </tbody>
    </table>
  </>
);
